<template>
  <div id="navbar">

    <ul class="nav-list">

      <li id="nav-list-name">
        <p id="name">Hitesh Gohil</p>
      </li>

      <hr>

      <li @click="selectHome()">
        <span :class="[{active: homeStatus}]">Home</span>
      </li>

      <hr>

      <li @click="selectAbout()">
        <span :class="[{active: aboutStatus}]">About</span>
      </li>

      <hr>

      <li @click="selectContact()">
        <span :class="[{active: contactStatus}]">Contact</span>
      </li>

      <hr>
      
    </ul>

    <div class="nav-small">

      <p id="nav-small-name">Hitesh Gohil</p>

      <div id="nav-small-sign" @click="toggleOverlay()">
        <i class="fas fa-bars"></i>
      </div>

    </div>

    <div class="nav-small-menu" :class="[{active: navOverlay}]">

      <i class="fas fa-times" @click="toggleOverlay()" :overlay="closeOverlay"></i>      

      <ul class="nav-small-list">

        <li @click="[selectHome(), closeOverlay()]">
          <span :class="[{active: homeStatus}]">Home</span>
        </li>

        <li @click="[selectAbout(), closeOverlay()]">
          <span :class="[{active: aboutStatus}]">About</span>
        </li>

        <li @click="[selectContact(), closeOverlay()]">
          <span :class="[{active: contactStatus}]">Contact</span>
        </li>

      </ul>
    </div>

  </div>
</template>

<script>
export default {
  name: 'Navbar',
  data(){
    return{
      navOverlay: false
    }
  },
  props: {
    selectHome: Function,
    selectAbout: Function,
    selectContact: Function,
    homeStatus: Boolean,
    aboutStatus: Boolean,
    contactStatus: Boolean
  },
  methods: {
    navMenu(){
      var button = document.querySelector('.menu-icon');
      button.addEventListener('click', function (){
        button.classList.toggle('open');
      });
    },
    toggleOverlay(){
      this.navOverlay = !this.navOverlay;
      console.log(`toggleOverlay: ${this.navOverlay}`);
    },
    closeOverlay(){
      this.navOverlay = false;
    }
  }
  /*
  
  */
}
</script>

<style scoped>

  @media only screen and (min-width: 700px){
    ul{
      padding: 0 !important;
      margin-top: 35%;
    }

    li{
      list-style-type: none;
      font-family: sans-serif;
      font-size: 1.15em;
      margin: 1em 0em;
    }

    li> span:hover{
      cursor: pointer;
    }

    li:hover > span{
      transition: 0.2s ease-in;
      color: rgb(0, 195, 255);
    }

    span{
      color: rgb(104, 152, 224);
    }

    #name{
      font-size: 1.2em;
      text-shadow: 0.5px 0.5px 1px rgb(93, 115, 121);
      color: rgb(100, 100, 100);
      font-family: 'Ubuntu', sans-serif;
      font-weight: 500;
      padding-bottom: 3em;
    }

    .active{
      box-sizing: border-box;
      transition: 1s ease-in-out;
      transition-timing-function: ease-in;
      color:rgb(0, 162, 255);
      border-right: 1.5px solid rgb(124, 188, 207);
      padding: 0.05em 0.4em;
      border-radius: 5px;
    }

    hr { 
      border: 0; 
      height: 1px; 
      background: rgba(0,89,255, 0.13)
    }

    .nav-small, .nav-small-menu{
      display: none;
    }


  }

    @media only screen and (max-width: 700px){

      /* .nav-small{

      } */

      .nav-list{
        display: none;
      }

      #nav-small-name{
        padding-left: 10px;
        float: left;
      }

      #nav-small-sign{
        float: right;
      }

      /* nav menu items */

      .nav-small-menu{
        display: none;
        z-index: 99;
        text-align: center;
        transition: all 10.3s;
        box-shadow: 10px 15px 50px 20px rgb(55, 61, 66);

        position:fixed;
        height:100%;
        width:100%;
        background:rgba(80, 80, 80, 0.9);
        overflow:auto;
        z-index:99;
      }

      .nav-small-list{
        color: white;
        list-style-type: none;
        text-align: center;
        padding-top: 3em;
      }

      .nav-small-list > li{
        padding: 1em 0em;
        margin: 1em 0;
        margin-right: 6em;
        margin-left: 4em;
        background-color: rgb(75, 75, 75);
        border-radius: 10px;
        font-weight: lighter;
      }

      .active{
        color: rgb(84, 166, 172) !important;
        display: block !important;
      }

      .fa-bars{
        margin: 1em;
        color: rgb(0, 195, 255);
        /* display: none; */
      }

      .fa-times{
        color: red;
        float: right;
        margin: 1em;
      }

    }
</style>


